@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("PT Mono"), url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
}

body {
  margin: 0;
  font-family: "Roboto Condensed", sans-serif;
  color: #041827;
  height: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1492px;
  width: 1240px;
}

.button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  display: block;
  text-align: center;
  text-transform: uppercase;
  color: #f2f5f6;
  border-radius: 3px;
  border: none;
  text-decoration: none;
}

.button:hover,
.button:focus {
  opacity: 0.6;
}

.button:active {
  opacity: 0.3;
}

.header {
  background: url("/img/header/header-background.png") no-repeat center top, url("/img/header/promo-backgraund.png") no-repeat center top;
}

.header__wrapper {
  min-height: 880px;
  width: 1220px;
  padding-right: 50px;
  padding-left: 0;
}

.header__top {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 30px 0 35px 0;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.header__logo-img {
  width: 215px;
  height: 55px;
}

.header__contacts {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.header__link {
  position: relative;
  font-size: 16px;
  line-height: 18px;
  color: #f2f5f6;
  font-family: "PT Mono", "Arial", sans-serif;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  text-decoration: none;
}

.header__link:hover,
.header__link:focus {
  opacity: 0.6;
}

.header__link--phone {
  margin-left: 125px;
}

.header__link--phone::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  top: 0;
  left: -34px;
  background: url("../img/icon-phone.svg") no-repeat center;
  background-size: 20px 20px;
}

.header__link--email::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  top: 0;
  left: -39px;
  background: url("../img/icon-mail.svg") no-repeat center;
  background-size: 20px 20px;
}

.header__button {
  margin-left: 145px;
  background: none;
  padding: 10px 15px;
  border: 1px solid #f2f5f6;
}

.promo {
  margin-left: 125px;
}

.promo__title {
  text-transform: uppercase;
  font-size: 80px;
  line-height: 94px;
  font-weight: 700;
  letter-spacing: 0.07em;
  color: #f2f5f6;
  margin: 185px 0 40px 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
}

.promo__description {
  position: relative;
  max-width: 685px;
  margin-bottom: 40px;
  color: #f2f5f6;
  font-size: 22px;
  line-height: 34px;
  font-family: "PT Mono", "Arial", sans-serif;
}

.promo__button {
  padding: 15px 25px;
  font-size: 16px;
  line-height: 19px;
  background: #00b2ff;
  max-width: 410px;
}

.promo__button-text--mobile {
  display: none;
}

.promo__button-text--tablet {
  display: block;
}

.advantages {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 24px;
  color: #041827;
}

.advantages__wrapper {
  padding: 120px 0;
}

.advantages__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.advantages__item {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin-bottom: 30px;
  padding: 40px 43px;
  display: block;
  width: 48%;
  min-height: 300px;
  border: 1px solid #ebebeb;
  border-left: 5px solid #00b2ff;
  background-color: #ffffff;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.advantages__item:nth-last-child(-n+2) {
  margin-bottom: 0;
}

.advantages__item:hover {
  background-color: #00b2ff;
  color: #f2f5f6;
}

.advantages__description {
  font-size: 16px;
  font-family: "PT Mono", "Arial", sans-serif;
  margin: 0;
}

.advantages__type {
  margin: 20px 0 15px;
  position: relative;
  font-size: 24px;
  color: #00b2ff;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.advantages__item:hover .advantages__type {
  color: #f2f5f6;
}

.advantages__icon path {
  fill: #00b2ff;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.advantages__item:hover .advantages__icon path {
  fill: #ffffff;
}

@media (max-width: 1023px) {
  .wrapper {
    max-width: 100%;
    width: 100%;
  }

  .header__wrapper {
    min-height: 640px;
    width: 670px;
    padding: 0;
  }

  .header__contacts {
    margin-top: 15px;
    -ms-flex-item-align: start;
    align-self: flex-start;
  }

  .header__contacts-text {
    display: none;
  }

  .header__link--phone {
    margin-left: 45px;
  }

  .header__button {
    display: none;
  }

  .promo {
    margin: 0;
  }

  .promo__title {
    margin: 80px 0 0 0;
    font-size: 64px;
    line-height: 70px;
  }

  .promo__description {
    margin: 20px 0 50px;
    font-size: 22px;
    line-height: 28px;
    max-width: 640px;
  }

  .promo__button {
    margin: 0;
    padding: 15px 25px;
  }

  .advantages__wrapper {
    padding: 80px 0;
  }

  .advantages__item {
    padding: 40px 30px 35px 45px;
    width: 48%;
    min-height: 320px;
  }

  .advantages__type {
    margin: 0 0 9px;
  }
}

@media (max-width: 767px) {
  .wrapper {
    width: 100%;
  }

  .header__wrapper {
    min-height: 560px;
    width: 270px;
  }

  .header__logo-img {
    width: 38px;
    height: 38px;
  }

  .header__contacts {
    margin-top: 10px;
  }

  .header__link--phone {
    margin-left: 50px;
  }

  .promo__title {
    margin: 80px 0 0;
    font-size: 36px;
    line-height: 42px;
    max-width: 100%;
  }

  .promo__description {
    margin: 20px 0 50px 0;
    font-size: 14px;
    line-height: 25px;
    max-width: 100%;
  }

  .promo__button {
    padding: 10px 30px;
  }

  .promo__button-text--mobile {
    display: block;
  }

  .promo__button-text--tablet {
    display: none;
  }

  .advantages__wrapper {
    padding: 50px 0;
  }

  .advantages__item {
    margin-bottom: 20px;
    padding: 30px 30px 35px 30px;
    width: 80%;
  }

  .advantages__item:nth-last-child(-n+2) {
    margin-bottom: 30px;
  }

  .advantages__item:last-child {
    margin-bottom: 0;
  }

  .advantages__type {
    margin: 6px 0 10px;
    font-size: 20px;
    line-height: 28px;
  }
}